@import "https://cdnjs.cloudflare.com/ajax/libs/modern-normalize/1.1.0/modern-normalize.min.css";
@import "https://fonts.googleapis.com/css2?family=Inter:wght@500;700&family=Montserrat:wght@400;500;700&display=swap";
body {
  font-family: Montserrat, sans-serif;
}

button:hover {
  cursor: pointer;
}

.container {
  min-width: 320px;
  max-width: 450px;
  margin: 0 auto;
  padding: 0 15px;
}

.button {
  color: #fff;
  background-color: #e86f54;
  border: none;
  border-radius: 38px;
  padding: 14px 28px;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
}

.button:hover {
  background-color: #e25b41;
}

.logo {
  fill: currentColor;
  color: #0e0e0e;
}

.form-field {
  color: #5c5c5c;
  flex-direction: column;
  row-gap: 8px;
  font-size: 14px;
  font-weight: 500;
  display: flex;
  position: relative;
}

.form-field:has(.form-field__text-input:not(:placeholder-shown):valid) {
  color: green;
}

.form-field:has(.form-field__text-input:not(:placeholder-shown):valid) .form-field__text-input {
  border: 1px solid green;
}

.form-field:has(.form-field__text-input:not(:placeholder-shown):valid) .form-field__correct-state-icon {
  fill: green;
  display: block;
}

.form-field:has(.form-field__text-input:not(:placeholder-shown):invalid) {
  color: red;
}

.form-field:has(.form-field__text-input:not(:placeholder-shown):invalid) .form-field__text-input {
  border: 1px solid red;
}

.form-field:has(.form-field__text-input:not(:placeholder-shown):invalid) .form-field__error-state-icon {
  fill: red;
  display: block;
}

.form-field__text-input {
  color: #111;
  border: 1px solid #d7d7d7;
  border-radius: 4px;
  padding: 16px;
}

.form-field__text-input::placeholder {
  color: #1116;
}

.form-field:hover {
  color: #111111b3;
}

.form-field:hover .form-field__text-input {
  border: 1px solid #111111b3;
}

.form-field__correct-state-icon, .form-field__error-state-icon {
  display: none;
  position: absolute;
  bottom: 14px;
  right: 16px;
}

.section {
  padding: 60px 0;
}

.section__heading {
  width: 100%;
  color: #000;
  text-transform: uppercase;
  margin-top: 32px;
  font-size: 24px;
  font-weight: 700;
  display: flex;
}

.section__heading:after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #000;
  align-self: center;
  margin-left: 14px;
}

.global-components, .global-components :not(h1, h3, p) {
  border: 1px solid red;
}

.products {
  flex-wrap: wrap;
  gap: 16px;
  padding: 0;
  list-style: none;
  display: flex;
}

.products__item {
  flex-basis: calc(50% - 8px);
}

.product-card {
  margin: 0;
}

.product-card__image {
  max-width: 100%;
  display: block;
}

.product-card__heading {
  margin: 12px 0 6px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.25;
}

.product-card__price {
  opacity: .6;
  margin: 0;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.25;
}

.store__button {
  margin: 0 auto;
  display: block;
}

.backdrop {
  width: 100vw;
  height: 100vh;
  background-color: #0000004d;
  position: fixed;
  top: 0;
  left: 0;
}

.contact {
  width: 272px;
  background-color: #fff;
  margin: 0 auto;
  padding: 28px 26px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.contact__heading {
  margin: 0 0 14px;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.25;
}

.contact__description {
  color: #5c5c5c;
  font-size: 12px;
}

.is-hidden {
  display: none;
}

.close-btn {
  background-color: #0000;
  border: none;
  position: fixed;
  top: 16px;
  right: 16px;
}

.contact__form-field {
  margin-bottom: 14px;
  font-size: 10px;
}

.contact__text-input {
  padding: 10px 12px;
}

.contact__button {
  width: 100%;
  border-radius: 8px;
  margin-top: 10px;
  padding: 10px 0;
  font-size: 14px;
}

